body {
  margin: 0;
  padding: 0;
  /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    */
    font-family: 'Roboto Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    box-sizing: border-box;
}

html, body {
    height: 100%;
    min-height: 100%;
}

body {
    height: 100%;
}

#root, #root > div {
    width: 100%;
    height: 100%;
}

#root > div {
    display: -webkit-flex;
    display: flex;
}

 .file-browser {
     background: rgb(39, 40, 34);
     color: white;
     padding: 0px 10px;
     width: 30%;
     min-width: 215px;
     max-width: 250px;
     height: 100%;
     overflow: auto;
     font-size: 14px;
}
 .file {
     color: #eee;
     display: block;
     list-style: none;
}
.file > a {
    text-decoration: none;
    color: white;
}
.file > a.active {
    color: #00ADB5;
    font-weight: bolder;
}
 .folder {
     list-style: none;
     cursor: pointer;
     margin: 4px 0;
}
 .folder > ul {
     display: none;
}
 .folder:before {
     padding: 5px;
     height: 10px;
     width: 10px;
     text-align: center;
     line-height: 10px;
     background: #5f6f72;
     border-radius: 1px;
     display: inline-block;
     content: "+";
     margin-right: 10px;
}
 .folder.folder-open > ul {
     display: block;
     padding-left: 15px;
     margin-left: 9px;
     border-left: 2px solid #5f6f72;
     padding-top: 7px;
     padding-bottom: 7px;
}
 .folder.folder-open:before {
     content: "-";
}
 

.no-file {
    height: 100%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    position: relative;
    background-color: #2F3129;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.no-file__msg {
    border: 1px solid #00ADB5;
    border-radius: 3px;
    padding: 20px;
    color: white;
    width: 40%;
}

.no-file-header, .no-file-footer {
    font-weight: bolder;
    color: #00ADB5;
    font-size: 18px;
    text-transform: uppercase;
}

.no-file-header {
    text-align: center;
}

.editor {
    height: 100%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    position: relative;
    padding-top: 29px;
}

.terminal {
    position: relative;
    height: 100%;
    width: 50%;
    background-color: black;
    padding: 0;
    margin: 0;
    overflow: auto;
    color: white;
}

.ace_editor {
    height: 100% !important;
    width: 50% !important;
}

:root {
    --loading-timeout: 2s;
    --loading-frac: calc(var(--loading-timeout) / 4);
}

.loader {
    position: relative;
}

@-webkit-keyframes loading {
    0% {opacity: 0; -webkit-transform: scale(1.0); transform: scale(1.0);}
    12.5% {opacity: 1; -webkit-transform: scale(1.3); transform: scale(1.3);}
    25% {opacity: 0; -webkit-transform: scale(1.0); transform: scale(1.0);}
}

@keyframes loading {
    0% {opacity: 0; -webkit-transform: scale(1.0); transform: scale(1.0);}
    12.5% {opacity: 1; -webkit-transform: scale(1.3); transform: scale(1.3);}
    25% {opacity: 0; -webkit-transform: scale(1.0); transform: scale(1.0);}
}

.loader .hands:before {
    display: block;
    content: '\1F64C';
    -webkit-animation: loading 2s ease-in-out calc(0*calc(2s / 4)) infinite;
            animation: loading 2s ease-in-out calc(0*calc(2s / 4)) infinite;
    -webkit-animation: loading var(--loading-timeout) ease-in-out calc(0*var(--loading-frac)) infinite;
            animation: loading var(--loading-timeout) ease-in-out calc(0*var(--loading-frac)) infinite;
}

.loader .hat:before {
    display: block;
    content: '\1F389';
    -webkit-animation: loading 2s ease-in-out calc(1*calc(2s / 4)) infinite;
            animation: loading 2s ease-in-out calc(1*calc(2s / 4)) infinite;
    -webkit-animation: loading var(--loading-timeout) ease-in-out calc(1*var(--loading-frac)) infinite;
            animation: loading var(--loading-timeout) ease-in-out calc(1*var(--loading-frac)) infinite;
}

.loader .confetti:before {
    display: block;
    content: '\1F38A';
    -webkit-animation: loading 2s ease-in-out calc(2*calc(2s / 4)) infinite;
            animation: loading 2s ease-in-out calc(2*calc(2s / 4)) infinite;
    -webkit-animation: loading var(--loading-timeout) ease-in-out calc(2*var(--loading-frac)) infinite;
            animation: loading var(--loading-timeout) ease-in-out calc(2*var(--loading-frac)) infinite;
}

.loader .balloon:before {
    display: block;
    content: '\1F388';
    -webkit-animation: loading 2s ease-in-out calc(3*calc(2s / 4)) infinite;
            animation: loading 2s ease-in-out calc(3*calc(2s / 4)) infinite;
    -webkit-animation: loading var(--loading-timeout) ease-in-out calc(3*var(--loading-frac)) infinite;
            animation: loading var(--loading-timeout) ease-in-out calc(3*var(--loading-frac)) infinite;
}

.emoji {
    position: absolute;
    top: 0;
    left: 0;
    font-size: 2rem;
}

.emoji:before {
    opacity: 0;
}



.terminal-bar {
    color: white;
    position: absolute;
    top: 0;
    left: 0;
    border-bottom: 1px solid black;
    padding: 5px;
    background-color: #2F3129;
    width: 100%;
    font-size: 10px;
    text-transform: uppercase;
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    border-bottom: 2px solid #00ADB5;
}

.terminal-bar-header {
    width: 50%;
    color: #00ADB5;
    font-weight: bolder;
    text-transform: none;
}

.terminal-bar-header:before {
    display: inline-block;
    content: '\1F389';
    padding-right: 5px;
}
.terminal-bar-header:after {
    display: inline-block;
    content: '\1F389';
    padding-left: 5px;
}

.terminal-bar-buttons {
    width: 50%;
    text-align: right;
    cursor: pointer;
}

.terminal-bar-buttons--inactive {
    opacity: 0.3;
}

.terminal-bar-buttons > div {
    display: inline-block;
    padding: 0 10px;
    font-weight: bolder;
    background-color: white;
    color: #00ADB5;
    margin: 0 10px;
}

.terminal-bar-buttons > div:last-child {
    margin-right: 0;
}

.terminal-bar-run:before {
    display: inline-block;
    content: '\1F3C3   ';
    padding-right: 5px;
}

.terminal-bar-test:before {
    display: inline-block;
    content: '\1F52C   ';
    padding-right: 5px;
}

